<template>
  <div id="tab-container">
    <gaga-list active :gagas="gagas"></gaga-list>
    <message></message>
    <person></person>
  </div>
</template>
<script>
import gagaList from "../components/GagaList";
import message from "../components/Message";
import person from "../components/Person";
import gagaData from "../json/gaga.json";
export default {
  data() {
    return {
      glist: "",
      gmsg: "",
      gperson: "",
      tabCont: "",
      headLogo: "",
      headMsg: "",
      gagas: gagaData.gagas
    };
  },
  components: {
    gagaList,
    message,
    person
  },
  mounted() {
    this.glist = document.getElementById("glist");
    this.gmsg = document.getElementById("gmsg");
    this.gperson = document.getElementById("gperson");
    this.tabCont = document.getElementById("tab-container");
    this.headLogo = document.getElementById("head-logo");
    this.headMsg = document.getElementById("head-msg");
  },
  props: ["selected"],
  watch: {
    selected: function(newVal, oldVal) {
      document.getElementById("g" + oldVal).removeAttribute("active");
      document.getElementById("g" + newVal).setAttribute("active", "");
      if (newVal == "list") {
        this.glist.style.transform = "translateX(0)";
        this.gmsg.style.transform = "translateX(0)";
        this.gperson.style.transform = "translateX(0)";
        document.getElementById("gheader").removeAttribute("hide");
        this.headLogo.style.transform = "translateX(0)";
        this.headMsg.style.transform = "translateX(0)";
      } else if (newVal == "msg") {
        this.glist.style.transform = "translateX(-100vw)";
        this.gmsg.style.transform = "translateX(-100vw)";
        this.gperson.style.transform = "translateX(-100vw)";
        document.getElementById("gheader").removeAttribute("hide");
        this.headLogo.style.transform = "translateX(-64px)";
        this.headMsg.style.transform = "translateX(-64px)";
      } else {
        this.glist.style.transform = "translateX(-200vw)";
        this.gmsg.style.transform = "translateX(-200vw)";
        this.gperson.style.transform = "translateX(-200vw)";
        document.getElementById("gheader").setAttribute("hide", "");
        this.headLogo.style.transform = "translateX(-128px)";
        this.headMsg.style.transform = "translateX(-128px)";
      }
    }
  }
};
</script>
<style scoped>
#tab-container {
  box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-flow: row nowrap;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
#glist,
#gmsg,
#gperson {
  position: absolute;
  padding: 50px 0;
  display: block;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  transition: all 0.4s;
}
#glist {
  left: 0;
}
#gmsg {
  left: 100vw;
}
#gperson {
  left: 200vw;
  top: -50px;
  padding-bottom: 0;
}
/* #gperson[active] {
  top: -60px;
} */
#glist::-webkit-scrollbar,
#gmsg::-webkit-scrollbar,
#gperson::-webkit-scrollbar {
  display: none;
}
</style>