import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './iconfont/iconfont.css'
import './css/animation.css'
import MintUI from 'mint-ui'

Vue.use(MintUI);
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
